<template>
    <yd-layout>
        <select-Leaders :approver="approver" @getLeaderInfo="getLeaderInfo" v-if="approver.length!=0"></select-Leaders>
        <yd-tabbar slot="tabbar">
            <div class="agree" @click="sendLeader()">送领导审批</div>
            <div class="back" @click="$router.go(-1)">返回</div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import selectLeaders from '../selectLeader/selectLeader.vue';
import { mapGetters } from 'vuex';
export default {
    name: '',
    components: {
        selectLeaders
    },
    data() {
        return {
            leaderNum: 99,
            approver: [],
            leaderIDs: ''
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('快捷审批流程');
        vm.approver = vm.getIncomeInfo.approver;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getIncomeInfo'])
    },
    methods: {
        getLeaderInfo(leaderIDs) {
            let vm = this;
            vm.leaderIDs = leaderIDs;
        },
        sendLeader() {
            let vm = this;
            if (vm.leaderIDs == '') {
                vm.$dialog.toast({
                    mes: '请选择审批人',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'crossSysSimAppr',
                    'approveType': 0,
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.getIncomeInfo.jsonData.reqdeptname + vm.getIncomeInfo.jsonData.requsername + '收入证明申请',
                    'ext': ''
                }, function() {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                }, false)
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "proofOfIncome.less";

.submit {
    background-color: #a4ceff;
}

.approval {
    height: .8rem !important;
    line-height: .8rem !important;
}

.name_circle ul li {
    margin-right: .03rem;
    margin-bottom: .2rem;
}
</style>


